import React from 'react';
import { Form } from 'react-bootstrap';

const Select = ({ optionArray, selectValue, value }) => {
  const style = { marginRight: '1.125rem' };
  return (
    /* SELECT COMPONENT,     */
    <Form.Select
      className="w-25 p-3"
      style={style}
      onChange={(e) => selectValue(e)}
      value={value}
    >
      {optionArray.map((v, i) => (
        <option value={v} key={i}>
          {/*   west  '', east  '',   */}
          {v === 'west' ? '' : v === 'east' ? '' : v}
        </option>
      ))}
    </Form.Select>
  );
};

export default Select;
