/* LIBRARY */
import React, { useState } from 'react';
import styled from 'styled-components';
import { Form } from 'react-bootstrap';

/* COMPONENT */
import Select from '../components/Trade/Select';
import Trade from '../components/Trade/Trade';
import Fresh from '../components/Trade/Fresh';

/* DATA */
import {
  westTradeData,
  eastTradeData,
} from '../components/Trade/data/tradeData';
import freshData from '../components/Trade/data/freshData';
import agingData from '../components/Trade/data/agingData';

/* STYLED-COMPONENT */
const Container = styled.div`
  padding: 1.125rem;
  width: 1320px;
  margin: auto;
  box-sizing: border-box;

  .explan {
    div {
      padding-bottom: 0.3rem;
    }
  }
  .fresh-table {
    cursor: pointer;
    width: 100%;
    margin-bottom: 0.5rem;
  }
  .trade-title {
    margin-bottom: 1.125rem;
    font-size: 2rem;
    font-weight: 700;
  }
`;
const InputContainer = styled.div`
  margin-top: 2.5rem;
  margin-bottom: 1.125rem;
  display: flex;
  .value {
    border: 0.5px solid #ced4da;
    border-radius: 5px;
    margin-right: 1.125rem;
  }
  .ques-icon {
    position: absolute;
    right: 10rem;
  }
  .check {
    width: 300px;
    margin: auto;
  }
`;

const TradeContainer = () => {
  const [continent, setContinent] = useState('west'); /*  */
  const [location, setLocation] = useState(' '); /*  */
  const [value, setValue] = useState(130); /*  */
  const [margin, setMargin] = useState(false); /*  */
  const [sell, setSell] = useState(false); /*    */
  const [fresh, setFresh] = useState(''); /*   */
  const [aging, setAging] = useState(' '); /*   */
  const [freshTableOver, setFreshTableOver] =
    useState(false); /*      ON/OFF */
  const [server, setServer] = useState('TOTAL'); /*    */

  /*     , Select  */
  const locationArray =
    continent === 'west'
      ? westTradeData.map((v) => v.location)
      : eastTradeData.map((v) => v.location);
  /*   ,        */
  const selectContinent = (e) => {
    setContinent(e.target.value);
    e.target.value === 'west'
      ? setLocation(' ')
      : setLocation(' ');
  };
  /*    */
  const selectLocation = (e) => setLocation(e.target.value);
  /*    */
  const selectFresh = (e) => setFresh(e.target.value);
  const selectAging = (e) => setAging(e.target.value);
  /*    */
  const valueChange = (e) => {
    0 < +e.target.value && e.target.value <= 130 && setValue(e.target.value);
  };
  /*   , TRUE/FALSE */
  const selectMargin = () => setMargin(!margin);
  /*     , TRUE/FALSE */
  const selectSell = () => setSell(!sell);
  return (
    <Container>
      {/*   */}
      <div className="explan">
        <h4 className="trade-title"></h4>
        <div>     .</div>
        <div>   =  x  x  2%</div>
        <div> =    -  </div>
        <span
          className="fresh-table"
          onClick={() => setFreshTableOver(!freshTableOver)}
        >
            
        </span>
        {freshTableOver && (
          <>
            <Fresh freshData={freshData} />
            <Fresh agingData={agingData} />
          </>
        )}
      </div>
      {/*   */}
      {/* SELECT START */}
      <InputContainer>
        <Select
          optionArray={['west', 'east']}
          selectValue={selectContinent}
          value={continent}
        ></Select>
        <Select
          optionArray={locationArray}
          selectValue={selectLocation}
          value={location}
        ></Select>
        <input
          onChange={(e) => valueChange(e)}
          value={value}
          type="number"
          className="w-50 p-3 value"
          size={10}
        />
        <Select
          optionArray={freshData.map((v) => v.freshName)}
          selectValue={selectFresh}
          value={fresh}
        />
        <Select
          optionArray={agingData.map((v) => v.freshName)}
          selectValue={selectAging}
          value={aging}
        />
        <div className="check">
          <Form>
            <Form.Check
              type="checkbox"
              id="real-sell"
              label="  "
              onClick={selectSell}
            />
            <Form.Check
              type="checkbox"
              id="margin"
              label=""
              onClick={selectMargin}
            />
          </Form>
        </div>
      </InputContainer>
      {/* SELECT END */}
      {/* TABLE START */}
      <Trade
        westTradeData={westTradeData}
        eastTradeData={eastTradeData}
        continent={continent}
        location={location}
        value={value}
        margin={margin}
        sell={sell}
        freshData={freshData}
        fresh={fresh}
        aging={aging}
      />
      {/* TABLE END */}
    </Container>
  );
};

export default TradeContainer;
