/* LIBRARY */
import React, { useMemo } from 'react';
import styled from 'styled-components';
import { Table } from 'react-bootstrap';

/* STYLED-COMPONENT */
const FreshComponent = styled.div`
  h3 {
    font-size: 1.5rem;
    font-weight: 700;
  }
`;
const Fresh = ({ freshData, agingData }) => {
  /*  TABLE   */
  const data = freshData || agingData;
  return (
    <FreshComponent>
      {/*     */}
      {freshData ? <h3>// </h3> : <h3> </h3>}
      <Table striped bordered hover className="w-75 p-3">
        <thead>
          <tr>
            <th></th>
            {/*      */}
            {data[0].pack.map((v, i) => (
              <th key={i}>{v.name}</th>
            ))}
          </tr>
        </thead>
        <tbody>
          {/*      */}
          {data.map((v, i) => (
            <tr key={i}>
              <td>{v.freshName}</td>
              {v.pack.map((c, j) => (
                <td key={j}>
                  {c.time >= 60 && !(c.time % 60)
                    ? `${c.time / 60}   `
                    : c.time >= 60 && c.time & 60
                    ? `${parseInt(c.time / 60)}   `
                    : `${c.time}   `}
                  {c.price > 0 ? `+${c.price}` : c.price}%
                </td>
              ))}
            </tr>
          ))}
        </tbody>
      </Table>
    </FreshComponent>
  );
};

export default Fresh;
